import { connect } from 'react-redux';
import LandingPromo from 'components/LandingNew/LandingPromo/LandingPromo';

import { getAllPromoGiveaways } from 'actions/insuranceActions';

// eslint-disable-next-line
const mapStateToProps = state => ({
  promoGiveawaysAll: state.insurance.promoGiveawaysAll,
});

const mapDispatchToProps = dispatch => ({
  doGetAllPromoGiveaways: () => {
    dispatch(getAllPromoGiveaways());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(LandingPromo);
