import _ from 'lodash';
import { withRouter } from 'react-router-dom';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import PropTypes from 'prop-types';

// import 'react-lazy-load-image-component/src/effects/opacity.css';
import './LandingPromo.scss';

class LandingPromo extends Component {
  componentDidMount() {
    const { doGetAllPromoGiveaways } = this.props;

    doGetAllPromoGiveaways();
  }

  renderPromos() {
    const { promoGiveawaysAll, history } = this.props;

    if (!promoGiveawaysAll || promoGiveawaysAll.length < 1) {
      return null;
    }

    try {
      return (
        <div className="promo-carousel-container">
          <ReactSlickCarousel
            lazyLoad
            className="white-sliders"
            autoplay
            arrows
            centerMode
            centerPadding="50px"
            slidesToShow={Math.min(4, promoGiveawaysAll.length)}
            responsive={[{
              breakpoint: 350,
              settings: {
                dots: true,
                arrows: false,
                className: 'green-dots',
                slidesToShow: Math.min(1, promoGiveawaysAll.length),
                centerMode: true,
                centerPadding: '50px',
              },
            }, {
              breakpoint: 576,
              settings: {
                dots: true,
                arrows: false,
                className: 'green-dots',
                slidesToShow: Math.min(2, promoGiveawaysAll.length),
                centerMode: true,
                centerPadding: '50px',
              },
            }, {
              breakpoint: 992,
              settings: {
                dots: true,
                arrows: false,
                className: 'green-dots',
                slidesToShow: Math.min(3, promoGiveawaysAll.length),
                centerMode: true,
                centerPadding: '50px',
              },
            }, {
              breakpoint: 1200,
              settings: {
                dots: true,
                arrows: false,
                className: 'green-dots',
              },
            }]}
          >
            {
              _.map(promoGiveawaysAll, (data, index) => (
                <button
                  type="button"
                  onClick={() => {
                    if (data.cta_url) {
                      try {
                        const splitted = data.cta_url.split('policypal.com');
                        history.push(splitted.length > 1 ? splitted[splitted.length - 1] : data.cta_url);
                      } catch (error) {
                        window.location.href = data.cta_url;
                      }
                    }
                  }}
                  key={`promo-img-${index}`}
                >
                  <LazyLoadImage
                    className="promo-img"
                    src={data.image_url}
                    alt={`promo-img-${index}`}
                    // effect="opacity"
                  />
                </button>
              ))}
          </ReactSlickCarousel>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    return (
      <Row className="landing-promo-container" noGutters>
        <Col className="header-padding" xs={12}>
          <h2>Promotions this month </h2>
        </Col>
        <Col xs={12}>
          {this.renderPromos()}
        </Col>
      </Row>
    );
  }
}

LandingPromo.defaultProps = {
  promoGiveawaysAll: null,
};

LandingPromo.propTypes = {
  history: PropTypes.object.isRequired,
  doGetAllPromoGiveaways: PropTypes.func.isRequired,
  promoGiveawaysAll: PropTypes.array,
};

export default withRouter(LandingPromo);
